* {
  font-family: "Inter";
}
body {
  background-color: #e7f6ff;
  margin: 0;
  font-family: "Inter", -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto",
    "Oxygen", "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

.wrapper {
  background: #ffffff;
  box-shadow: 0px 10px 20px rgba(0, 0, 0, 0.04);
  border-radius: 20px;
  max-width: 1080px;
  margin: 50px auto;
}

header {
  border-bottom: 1px solid #eaeaea;
  h3,
  p {
    margin: 0;
  }
  img {
    margin-right: 15px;
  }
}

.content {
  h1 {
    margin: 0;
  }
}

.search-block {
  border: 1px solid #f3f3f3;
  border-radius: 10px;
  padding: 0 13px;
  position: relative;
  input {
    border: 0;
    padding: 10px;
    font-size: 16px;
    width: 200px;
  }
  .clear {
    position: absolute;
    width: 18px;
    height: 18px;
    right: 10px;
    top: 10px;
  }
}

.cartItem {
  border: 1px solid #f3f3f3;
  border-radius: 20px;
  overflow: hidden;
  padding: 20px;

  p {
    font-size: 16px;
    margin: 0;
  }

  b {
    font-size: 14px;
  }
  .removeBtn {
    opacity: 0.5;
    cursor: pointer;
    transition: opacity 0.15 ease-in-out;
    &:hover {
      opacity: 1;
    }
  }
}

.items {
  flex: 1;
  overflow: auto;
  margin-bottom: 40px;
}

.btnGreen {
  width: 100%;
  height: 55px;
  background: #9dd558;
  border-radius: 18px;
  border: 0;
  color: #fff;
  font-size: 16px;
  font-weight: 500;
  cursor: pointer;
  transition: background 0.1s ease-in-out;
  &:hover {
    background: lighten($color: #9dd558, $amount: 5%);
  }
  &:active {
    background: darken($color: #9dd558, $amount: 5%);
  }
  &:disabled {
    background-color: #bebebe;
    cursor: default !important;
  }
}
.cartTotal {
  .btnGreen {
    position: relative;

    &:hover {
      img {
        transform: translateX(5px);
      }
    }
    img {
      position: absolute;
      transition: transform 0.15 ease-in-out;
      right: 30px;
      top: 20px;
    }
  }
  ul {
    margin-bottom: 40px !important;
  }

  li {
    display: flex;
    align-items: flex-end;
    margin-bottom: 20px;
    div {
      flex: 1;
      height: 1px;
      border-bottom: 1px dashed #dfdfdf;
      position: relative;
      top: -4px;
      margin: 0 7px;
    }
  }
}

.cartEmpty {
  text-align: center;

  p {
    width: 280px;
    line-height: 24px;
  }

  .btnGreen {
    width: 245px;
    margin-top: 20px;

    &:hover {
      img {
        transform: translateX(3px);
      }
    }

    img {
      position: relative;
      top: 1px;

      margin-right: 15px;
      transition: transform 0.15s ease-in-out;
    }
  }
}
